import { createApp } from 'vue';
import App from './App.vue';

// AMD module export for Moodle
define(['exports'], function(exports) {
    /**
     * Initialize the Mosaic board Vue app
     * @param {Object} config Configuration from PHP
     */
    exports.init = function(config) {
        // Wait for DOM to be ready
        if (document.readyState === 'loading') {
            document.addEventListener('DOMContentLoaded', () => initApp(config));
        } else {
            initApp(config);
        }
    };

    /**
     * Initialize the Vue application
     * @param {Object} config Configuration object
     */
    function initApp(config) {
        const container = document.getElementById('mosaic-board-app');
        if (!container) {
            console.error('Mosaic: Container element not found');
            return;
        }

        // Create and mount Vue app
        const app = createApp(App, { config });
        app.mount(container);
    }
});
