// We'll use Moodle's core/ajax for AJAX calls
let Ajax;

// Dynamically import Moodle's Ajax module
if (typeof require !== 'undefined') {
    require(['core/ajax'], (ajax) => {
        Ajax = ajax;
    });
}

/**
 * Call a Moodle web service
 * @param {string} methodname Web service method name
 * @param {Object} args Arguments
 * @returns {Promise} Promise resolving to the result
 */
async function callWebService(methodname, args = {}) {
    return new Promise((resolve, reject) => {
        if (!Ajax) {
            // Fallback: use fetch if Ajax module not loaded
            const url = M.cfg.wwwroot + '/lib/ajax/service.php';
            const formData = new FormData();
            formData.append('sesskey', M.cfg.sesskey);
            formData.append('info', methodname);

            const request = {
                index: 0,
                methodname: methodname,
                args: args,
            };
            formData.append('args', JSON.stringify([request]));

            fetch(url, {
                method: 'POST',
                body: formData,
            })
            .then(response => response.json())
            .then(data => {
                if (data[0].error) {
                    reject(new Error(data[0].exception.message));
                } else {
                    resolve(data[0].data);
                }
            })
            .catch(reject);
        } else {
            Ajax.call([{
                methodname: methodname,
                args: args,
            }])[0].done(resolve).fail(reject);
        }
    });
}

/**
 * Load board data
 * @param {number} boardid Board ID
 * @returns {Promise} Board data
 */
export async function loadBoard(boardid) {
    return await callWebService('mod_mosaic_get_board', { boardid });
}

/**
 * Create a new card
 * @param {Object} cardData Card data
 * @returns {Promise} Created card
 */
export async function createCard(cardData) {
    return await callWebService('mod_mosaic_create_card', cardData);
}

/**
 * Update a card
 * @param {number} cardid Card ID
 * @param {Object} updates Update data
 * @returns {Promise} Updated card
 */
export async function updateCard(cardid, updates) {
    return await callWebService('mod_mosaic_update_card', { cardid, ...updates });
}

/**
 * Delete a card
 * @param {number} cardid Card ID
 * @returns {Promise} Result
 */
export async function deleteCard(cardid) {
    return await callWebService('mod_mosaic_delete_card', { cardid });
}
